"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import styles from "./ProfileHeader.module.css";

import { getUserData } from "@/lib/auth";
import { getOrganizationProfileApi } from "@/lib/api/auth.api";

export default function ProfileHeader() {
  const router = useRouter();

  const [loading, setLoading] = useState(true);
  const [profile, setProfile] = useState(null);

  // ================= FETCH PROFILE =================
  useEffect(() => {
    async function fetchProfile() {
      try {
        const user = getUserData();
        if (!user?.identity) return;

        const res = await getOrganizationProfileApi(user.identity);
        if (res?.status) {
          setProfile(res.data);
        }
      } catch (err) {
        console.error("ProfileHeader error:", err);
      } finally {
        setLoading(false);
      }
    }

    fetchProfile();
  }, []);

  if (loading || !profile) return null;

  // ================= FALLBACK LETTER =================
  const firstLetter =
    profile.organizationName?.charAt(0)?.toUpperCase() ||
    profile.name?.charAt(0)?.toUpperCase() ||
    "U";

  return (
    <div className={styles.wrapper}>
      {/* COVER */}
      <div className={styles.cover} />

      {/* CONTENT */}
      <div className={styles.content}>
        {/* LEFT */}
        <div className={styles.left}>
          {profile.profileImage ? (
            <img
              src={profile.profileImage}
              alt="profile"
              className={styles.avatar}
            />
          ) : (
            <div className={styles.avatarFallback}>
              {firstLetter}
            </div>
          )}
        </div>

        {/* RIGHT */}
        <div className={styles.right}>
          <h2 className={styles.name}>
            {profile.organizationName || profile.name}
            <span className={styles.role}>
              ({profile.role || "User"})
            </span>
          </h2>

          <div className={styles.stats}>
            <span onClick={() => router.push("/dashboard/profile/followers")}>
              {profile.followersCount || 0} Followers
            </span>
            <span onClick={() => router.push("/dashboard/profile/following")}>
              {profile.followingCount || 0} Following
            </span>
          </div>

          <div className={styles.rank}>
            #{profile.rank || 0} Rank
          </div>
        </div>
      </div>
    </div>
  );
}
